import style from "./style.module.css";
import React from "react";
import axios from "axios";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import ReactModal from "react-modal";
ReactModal.setAppElement('*');

export default function DeletePost(props)
{
    const { isOpen , onRequestClose , contentLabel , deleteJob , setAllJobs } = props;

    async function deleteJobPost()
    {
        try
        {
            const res = await axios.delete(`/api/deletePostedJob/${deleteJob}` ,
            {
                headers : {
                    "auth-token" : JSON.parse(window.localStorage.getItem("auth-token"))["authToken"]
                }
            });
            toast.success(res.data);
            setAllJobs(video => (video.filter((item)=>{
                return item._id!==deleteJob
            })))
            onRequestClose();
        }
        catch(error)
        {
            console.log(error);
            toast.warn(error.response.data);
        }
    }
    return <>
        <ReactModal
            isOpen={isOpen}
            onRequestClose={onRequestClose}
            contentLabel={contentLabel}
            className={style.deletePostContentClass}
            overlayClassName={style.modalOverlayClass}
        >
            <div className={style.confirmation}>Are you surely want to delete the post ?</div>
            <div className={style.buttonContainer}>
                <button className={style.yes} onClick={deleteJobPost}>Yes</button>
                <button className={style.no} onClick={onRequestClose}>No</button>
            </div>
        </ReactModal>
        <ToastContainer theme="colored" autoClose={4000} hideProgressBar={true} />
    </>
}