import style from "./style.module.css";
import React , { useState } from "react";

import ReactModal from "react-modal";
ReactModal.setAppElement('*');

export default function Response(props)
{
    const [modal , manageModal] = useState(false);

    function openModal()
    {
        manageModal(true);
        document.body.style.overflow = 'hidden';
    }

    function closeModal()
    {
        manageModal(false);
        document.body.style.overflow = 'auto';
    }

    const {id , jobId , name , age , address , country , phoneNumber , email , resume , rejectResponse} = props ;
    return <>
        <div className={style.response}>
            <div className={style.glass}></div>
            <div className={style.content}>
                <div><span className={style.name}>Name</span> : {name}</div>
                <div><span className={style.age}>Age</span> : {age}</div>
                <div><span className={style.address}>Address</span> : {address}</div>
                <div><span className={style.country}>Country</span> : {country}</div>
                <div><span className={style.phoneNumber}>Phone Number</span> : {phoneNumber}</div>
                <div><span className={style.applicantEmail}>Email</span> : {email}</div>
                <div className={style.buttonContainer}>
                <button onClick={openModal} className={style.open}>Open Resume</button>
                <button onClick={()=>rejectResponse([id , jobId])} className={style.reject}>Reject</button>
                </div>
            </div>
        </div>
        <ReactModal
            isOpen={modal}
            onRequestClose={closeModal}
            contentLabel={"Example Modal"}
            className={style.modalContentClass}
            overlayClassName={style.modalOverlayClass}
        >
            <iframe
                title="PDf viewer"
                src={resume}
                style={{height : '100%' , width : '100%'}}
            ></iframe>
        </ReactModal>
    </>
};
