import axios from "axios";
import React , { useState , useEffect } from "react";
import { MdDelete } from "react-icons/md";
import { useTheme } from "../../Context/ThemeContext";

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import Response from "./Response";
import DeletePost from "./DeletePost";
import style from "./style.module.css";
import { Bars } from "react-loader-spinner";

import ReactModal from "react-modal";
ReactModal.setAppElement('*');

export default function PostedJobs(props)
{
    const [allJobs, setAllJobs] = useState(null);
    const [deleteModal , setDeleteModal] = useState(false);
    const [deleteJob , setdeleteJob] = useState(null);

    const { checked } = useTheme();

    function openModal(id)
    {
        setDeleteModal(true);
        setdeleteJob(id);
        document.body.style.overflow = 'hidden';
    }

    function closeModal()
    {
        setDeleteModal(false);
        setdeleteJob(null);
        document.body.style.overflow = 'auto';
    }

    useEffect(() => {
        async function getJobs()
        {
            const res = await axios.get("/api/getPostedJobs" ,
            {
                headers : {
                    "auth-token" : JSON.parse(window.localStorage.getItem("auth-token"))["authToken"],
                    "filters" : JSON.stringify(props.newFilters),
                    "search" : props.search
                }
            })
            console.log(res.data);
            setAllJobs(res.data);
        }
        getJobs()
    },[props.newFilters , props.search])

    async function rejectResponse(props)
    {
        try
        {
            const [id , jobId]=props;
            const res = await axios.patch("/api/removeApplication" ,
                {
                    id : id,
                    jobId : jobId
                },
                {
                    headers : {
                        "auth-token" : JSON.parse(window.localStorage.getItem("auth-token"))["authToken"],
                        "action" : false
                    }
            })
            toast.success(res.data);
        }
        catch(error)
        {
            console.log(error);
            toast.warn(error.response.data);
        }
    }

    return <>{!allJobs ? <Bars
        height="80"
        width="80"
        color={checked ? "purple" : "#4fa94d"}
        ariaLabel="bars-loading"
        wrapperStyle={{}}
        wrapperClass={style.loader}
        visible={true}
        /> :
        <>{allJobs.length>0 ? <div className={style.jobs}>
        {allJobs.map((item , index) => {
            return <div key={index}>
                <div className={style.job}>
                    <div className={style.title} style={{color : checked ? "white" : "black"}}>{item.title}</div>
                    <div className={style.description} style={{color : checked ? "aquamarine" : "#e22020"}}>{item.description}</div>
                    <div className={style.user} style={{color : checked ? "pink" : "#df56e4"}}><span>Posted by</span> : {item.user}</div>
                    <div className={style.location} style={{color : checked ? "wheat" : "#9824d1"}}>{item.email}</div>
                    <div className={style.container} style={{color : checked ? "yellow" : "#1da159"}}>
                        <div className={style.salary}><span>Salary</span> : {item.salary} &#8377;</div>
                        <div className={style.experience}><span>Past experience</span> : {item.experience} {item.experience > 1 ? 'years' : 'year'}</div>
                    </div>
                    <div className={style.container} style={{color : checked ? "yellow" : "#1da159"}}>
                        <div className={style.jobType}><span>Job type</span> : {item.jobType}</div>
                        <div className={style.workMode}><span>Work mode</span> : {item.workMode}</div>
                    </div>
                    <div className={style.location} style={{color : checked ? "wheat" : "#9824d1"}}>{item.location}</div>
                    <MdDelete className={style.delete} onClick={()=>openModal(item._id)} />
                </div>
                <div className={style.responses}>
                    {item.applications.map((appl , index) => {
                        return <Response id={appl.id} jobId={item._id} key={index} name={appl.name} age={appl.age} address={appl.address} country={appl.country} phoneNumber={appl.phoneNumber} email={appl.email} resume={appl.resume} rejectResponse={rejectResponse} />
                    })}
                    <ToastContainer theme="colored" autoClose={4000} hideProgressBar={true} />
                </div>
            </div>
        })}
        <DeletePost isOpen={deleteModal} onRequestClose={closeModal} contentLabel="Example Modal" deleteJob={deleteJob} setAllJobs={setAllJobs}/>
        </div> : <div className={style.empty} style={{color : checked ? "white" : "#04ad0d"}}>------ No Jobs Posted Yet ------</div>}</>}
    </>
}